.info-container {
  width: 100%;
  height: 20%;
  margin-top: 35px;
}

.teamOutlined {
  margin-left: 30px;
  font-size: 130px;
}

.about-b {
  font-size: 50px;
  font-family: "Noto Sans JP", sans-serif;
}

.buttooon {
  height: 40px;
  width: 100px;
  border: none;
  background: rgb(181, 34, 34);
  background: radial-gradient(
    circle,
    rgba(181, 34, 34, 1) 0%,
    rgb(147, 14, 14) 100%
  );
  padding: 10px;
  opacity: 1;
  color: white;
  font-weight: bolder;
  border-radius: 20px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.buttooon:hover {
  opacity: 0.8;
}

.buttooon:focus,
.buttooon:active {
  outline: none !important;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2), 0 3px 10px 0 rgba(0, 0, 0, 0.19);
}

.info-i {
  font-family: "Montserrat", sans-serif;
  font-size: 16px;
  margin-bottom: 35px;
}

@media only screen and (max-width: 800px) {
