.progressBarContainer {
  width: 100%;
  height: 150px;
}

.progressBar{
  padding-top: 50px;
  display: flex;
  flex-wrap: wrap;
  height: 120px;
}

.progressBarCommentContainer {
  width: 85%;
  margin-left: 5%;
  background-color: #545864;
  color: white;
  border-radius: 15px 0 0 15px;
  display: flex;
  margin-top: 30px;
  margin-bottom: 0;
  padding-bottom: 0;
}

.progressBarComment {
  display: flex;
  flex-wrap: wrap;
  margin: auto;
  font-size: 30px;
}

.outerBarContainer {
  width: 75%;
  margin-right: 5%;
  background-color: #d7d7d7;
  display: flex;
  border-radius: 0 15px 15px 0;
  height: auto;
}

.outerBar {
  height: 30%;
  width: 90%;
  background-color: white;
  margin: 0 5% 0 5%;
  align-self: center;
  border-radius: 10px;

  display: flex;
  position: relative;
}

.barPercentagePointer {
  position: absolute;
  top: -50px;
  right: -20px;
  width: 0;
  height: 0;
  border-left: 25px solid transparent;
  border-right: 25px solid transparent;
  border-top: 50px solid CornflowerBlue;
}

.barTextPercentage {
  position: absolute;
  top: -50px;
  right: -10px;
  color: white;
}

@media only screen and (max-width: 800px) {
  .progressBarContainer{
      height: 125px;
  }

  .progressBar{
    height: 100px;
  }

  .progressBarComment{
      margin: auto;
      font-size: 21px;
  }
 
  .outerBar{
      height: 40%;
  }
}
